/* eslint-disable no-console */
<template>
  <div class="mani-wrap" id="mani-wrap">
    <div class="shengming" @click="clickShengming">声明</div>
    <div class="chat-box">
      <div class="top-logo">
        <img src="./imgs/logo.png" alt="" srcset="">
      </div>
      <div class="hellow chat-item">
        <div class="item-box">
          <div class="item-right">   
            <span>我是小V疫情助理，很高兴为您服务！</span>
            <span>同舟共济，共克时艰，防控疫情人人有责。</span>
            <span>武汉加油，湖北加油，中国加油！</span>
          </div>
        </div>
      </div>
      <div class="chat-item" 
           :class="'chat-item-' + (index+1)"
           v-for="(item, index) in answersList" 
           :key="index">
        <div class="item-box">
          <div class="item-left">{{item.text}}</div>
        </div>
        <div class="item-box">
          <div class="item-right">
            <img class="width100" :src="item.answer[0]" alt />
          </div>
        </div>
      </div>
    </div>
    <div class="question-box">
      <div class="level1-box">
        <swiper
          class="swiper-box"
          :options="swiperOption"
          ref="mySwiper"
        >
          <!-- slides -->
          <swiper-slide 
            v-for="(item, index) in questions"
            :key="index">
            <div class="bottom-slide-item">
              <!-- <a :href="item.href" v-if="item.answerType==='href'">
                <img class="item-img" :src="item.icon" alt="">
              </a> -->
              <img  class="item-img" 
                    :class="index" 
                    :data-set="index"
                    :src="item.icon" alt="">
            </div>
          </swiper-slide>
         
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
import { questions } from "./config";
export default {
  name: "Content",
  data() {
    this.questions = questions;
    
    return {
      answersList: [],
      swiperOption: {
        // swiper options 所有的配置同swiper官方api配置
        loop: true,
        autoplay: {
          delay: 2000,//1秒切换一次
        },
        direction: "horizontal",
        slidesPerView: 5,
        centeredSlides: true,
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  methods: {
    clickShengming() {
      const shengming = {
        text: '声明',
        answerType: 'img',
        answer: [
            require('./imgs/shengming.jpg')
        ]
      }

      this.showAnswer(shengming)
    },
    handleClick(item) {      
      if(item.answerType === 'href') {
        this.jumpTo(item.href)
      } else {
        this.showAnswer(item)
      }
    },
    showAnswer(item) {
      // eslint-disable-next-line no-console
      this.answersList.push(item);
      sessionStorage.setItem('answersList', JSON.stringify(this.answersList))
      this.$nextTick(() =>{
        const className = '.chat-item-' + this.answersList.length
        const curItem = document.querySelector(className)
        const newScrollTop = curItem.getBoundingClientRect().top + this.chatBox.scrollTop - 10
        setTimeout(() => {
          $(this.chatBox).animate({
            scrollTop: newScrollTop
          },300)
           
        },200)

        
      })
    },
    jumpTo(href) {
      // eslint-disable-next-line no-console
      console.log(href);
      window.history.pushState({
        title: "title",
        url: "#"
      }, "title", "#");
      setTimeout(() => {window.location.href = href},100)
    }
  },
  created() {
    // let answersList = sessionStorage.getItem('answersList')
    // answersList = answersList ? JSON.parse(answersList) : []    
    // this.answersList = answersList
  },
  mounted() {
    // this.swiper.slideTo(4, 800, false)
    this.chatBox = document.querySelector('.chat-box');
    this.$nextTick(() => {
      $('.swiper-box').click((e) => {
        const key = $(e.target).attr('data-set')
        this.handleClick(this.questions[key])
      })
    })
  } 
};
</script>
